<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-1 col-sm-1 col-md-2"></div>
      <div class="col-xs-10 col-sm-10 col-md-8">
        <div class="top">
          <i class="el-icon-edit-outline"></i>
          <span class="tianxie">诉讼费计算信息：</span>
        </div>
        <div class="leixing_father">
          <span class="leixing">案件类型：</span>
          <el-select v-model="value" placeholder="财产案件" @change="changeValue">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
        <div class="sheji" v-if="shifou">
          <span class="sheji1">涉及财产：</span>
          <el-radio-group v-model="radio" @change="change">
            <el-radio label="1">是</el-radio>
            <el-radio label="2">否</el-radio>
          </el-radio-group>
        </div>
        <div class="edu" v-if="flag">
          <span class="edu1">输入标的额：</span>
          <el-input
            v-model="input0"
            placeholder="请输入金额"
            class="much"
            :disabled="edit"
            :views="jinyong()"
          ></el-input>
        </div>
        <el-row>
          <el-button type="primary" @click="jisuan">计算</el-button>
          <el-button type="primary" @click="chongzhi">重置</el-button>
        </el-row>
        <div class="jieguo">
          <i class="el-icon-edit-outline"></i>
          <span class="jieguo1">诉讼费计算结果：</span>
        </div>
        <div class="money">
          <div>
            <span class="three">受理费：</span>
            <el-input v-model="input1" placeholder class="much"></el-input>&nbsp;&nbsp;
            <span class="yuan">元</span>
          </div>
          <div>
            <span class="three">执行费：</span>
            <el-input v-model="input2" placeholder class="much"></el-input>&nbsp;&nbsp;
            <span class="yuan">元</span>
          </div>
          <span class="three">保全费：</span>
          <el-input v-model="input3" placeholder class="much"></el-input>&nbsp;&nbsp;
          <span class="yuan">元</span>
        </div>
      </div>
      <div class="col-xs-1 col-sm-1 col-md-2"></div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      options: [
        {
          value: "选项1",
          label: "财产案件"
        },
        {
          value: "选项2",
          label: "普通非财产案件"
        },
        {
          value: "选项3",
          label: "离婚案件"
        },
        {
          value: "选项4",
          label: "人格权案件"
        },
        {
          value: "选项5",
          label: "知识产权案件"
        },
        {
          value: "选项6",
          label: "劳动争议案件"
        },
        {
          value: "选项7",
          label: "行政案件"
        },
        {
          value: "选项8",
          label: "商标、专利、海事行政案件"
        },
        {
          value: "选项9",
          label: "管辖权异议不成立案件"
        }
      ],
      value: "",
      radio: "2",
      input0: "",
      input1: "",
      input2: "",
      input3: "",
      flag: false,
      edit: false,
      shifou: true
    };
  },
  methods: {
    jinyong() {
      if (this.value == "选项1") {
        this.edit = false;
      } else if (this.value == "选项2") {
        this.edit = true;
        this.input0 = "不涉及财产";
      } else if (this.value == "选项3") {
        this.edit = false;
      } else if (this.value == "选项4") {
        this.edit = false;
      } else if (this.value == "选项5") {
        this.edit = false;
      } else if (this.value == "选项6") {
        this.edit = true;
        this.input0 = "不涉及财产";
      } else if (this.value == "选项7") {
        this.edit = true;
        this.input0 = "不涉及财产";
      } else if (this.value == "选项8") {
        this.edit = true;
        this.input0 = "不涉及财产";
      } else if (this.value == "选项9") {
        this.edit = true;
        this.input0 = "不涉及财产";
      }
    },
    change() {
      if (this.radio == "1") {
        this.flag = true;
        this.$options.methods.jisuan();
      } else {
        this.flag = false;
      }
    },
    jisuan() {
      if (this.value == "选项1") {
        if (this.radio == "1") {
          var str = this.input0;
          if (str == "") {
            this.$message("输入金额不正确");
          }
          var num = parseInt(this.input0);
          if (num < 10000) {
            this.input1 = 50 + "";
          } else if (num >= 10000 && num < 100000) {
            this.input1 = num * 0.025 - 200 + "";
          } else if (num >= 100000 && num < 200000) {
            this.input1 = num * 0.02 + 300 + "";
          } else if (num >= 200000 && num < 500000) {
            this.input1 = num * 0.015 + 1300 + "";
          } else if (num >= 500000 && num < 1000000) {
            this.input1 = num * 0.01 + 3800 + "";
          } else if (num >= 1000000 && num < 2000000) {
            this.input1 = num * 0.009 + 4800 + "";
          } else if (num >= 2000000 && num < 5000000) {
            this.input1 = num * 0.008 + 6800 + "";
          } else if (num >= 5000000 && num < 10000000) {
            this.input1 = num * 0.007 + 11800 + "";
          } else if (num >= 10000000 && num < 20000000) {
            this.input1 = num * 0.006 + 21800 + "";
          } else if (num >= 20000000) {
            this.input1 = num * 0.005 + 31800 + "";
          }
          if (num < 10000) {
            this.input2 = "50";
          } else if (num >= 10000 && num < 500000) {
            this.input2 = num * 0.015 - 100 + "";
          } else if (num >= 500000 && num < 5000000) {
            this.input2 = num * 0.01 + 2400 + "";
          } else if (num >= 5000000 && num < 10000000) {
            this.input2 = num * 0.005 + 27400 + "";
          } else if (num >= 10000000) {
            this.input2 = num * 0.001 + 67400 + "";
          }
          if (num <= 1000) {
            this.input3 = "30";
          } else if (num > 1000 && num <= 100000) {
            this.input3 = num * 0.01 + 20 + "";
          } else if (num > 100000) {
            this.input3 = num * 0.005 <= 5000 ? num * 0.005 + 520 + "" : 5000;
          }
        } else if (this.radio == "2") {
          this.input1 = "50 - 100";
          this.input2 = "50 - 500";
          this.input3 = "30";
        }
      } else if (this.value == "选项2") {
        this.input1 = "50 - 100";
        this.input2 = "50 - 500";
        this.input3 = "30";
      } else if (this.value == "选项3") {
        if (this.radio == "1") {
          var str = this.input0;
          if (str == "") {
            this.$message("输入金额不正确");
          }
          var num = parseInt(this.input0);
          var num1, num2;
          if (num <= 200000) {
            this.input1 = "50 - 300";
          } else if (num > 200000) {
            num1 = (num - 200000) * 0.005 + 50 + "";
            num2 = (num - 200000) * 0.005 + 300 + "";
            this.input1 = num1 + " - " + num2;
          }
          if (num < 10000) {
            this.input2 = "50";
          } else if (num >= 10000 && num < 500000) {
            this.input2 = num * 0.015 - 100 + "";
          } else if (num >= 500000 && num < 5000000) {
            this.input2 = num * 0.01 + 2400 + "";
          } else if (num >= 5000000 && num < 10000000) {
            this.input2 = num * 0.005 + 27400 + "";
          } else if (num >= 10000000) {
            this.input2 = num * 0.001 + 67400 + "";
          }
          if (num <= 1000) {
            this.input3 = "30";
          } else if (num > 1000 && num <= 100000) {
            this.input3 = num * 0.01 + 20 + "";
          } else if (num > 100000) {
            this.input3 = num * 0.005 <= 5000 ? num * 0.005 + 520 + "" : 5000;
          }
        } else if (this.radio == "2") {
          this.input1 = "50 - 300";
          this.input2 = "50 - 500";
          this.input3 = "30";
        }
      } else if (this.value == "选项4") {
        if (this.radio == "1") {
          var str = this.input0;
          if (str == "") {
            this.$message("输入金额不正确");
          }
          var num = parseInt(this.input0);
          var num1, num2;
          if (num <= 50000) {
            this.input1 = "100 - 500";
          }
          if (num > 50000 && num <= 100000) {
            num1 = (num - 50000) * 0.01 + 100 + "";
            num2 = (num - 50000) * 0.01 + 500 + "";
            this.input1 = num1 + " - " + num2;
          } else if (num > 100000) {
            num1 = (num - 100000) * 0.005 + 100 + "";
            num2 = (num - 100000) * 0.005 + 500 + "";
            this.input1 = num1 + " - " + num2;
          }
          if (num < 10000) {
            this.input2 = "50";
          } else if (num >= 10000 && num < 500000) {
            this.input2 = num * 0.015 - 100 + "";
          } else if (num >= 500000 && num < 5000000) {
            this.input2 = num * 0.01 + 2400 + "";
          } else if (num >= 5000000 && num < 10000000) {
            this.input2 = num * 0.005 + 27400 + "";
          } else if (num >= 10000000) {
            this.input2 = num * 0.001 + 67400 + "";
          }
          if (num <= 1000) {
            this.input3 = "30";
          } else if (num > 1000 && num <= 100000) {
            this.input3 = num * 0.01 + 20 + "";
          } else if (num > 100000) {
            this.input3 = num * 0.005 <= 5000 ? num * 0.005 + 520 + "" : 5000;
          }
        } else if (this.radio == "2") {
          this.input1 = "100 - 500";
          this.input2 = "50 - 500";
          this.input3 = "30";
        }
      } else if (this.value == "选项5") {
        if (this.radio == "1") {
          var str = this.input0;
          if (str == "") {
            this.$message("输入金额不正确");
          }
          var num = parseInt(this.input0);
          if (num < 10000) {
            this.input1 = 50 + "";
          } else if (num >= 10000 && num < 100000) {
            this.input1 = num * 0.025 - 200 + "";
          } else if (num >= 100000 && num < 200000) {
            this.input1 = num * 0.02 + 300 + "";
          } else if (num >= 200000 && num < 500000) {
            this.input1 = num * 0.015 + 1300 + "";
          } else if (num >= 500000 && num < 1000000) {
            this.input1 = num * 0.01 + 3800 + "";
          } else if (num >= 1000000 && num < 2000000) {
            this.input1 = num * 0.009 + 4800 + "";
          } else if (num >= 2000000 && num < 5000000) {
            this.input1 = num * 0.008 + 6800 + "";
          } else if (num >= 5000000 && num < 10000000) {
            this.input1 = num * 0.007 + 11800 + "";
          } else if (num >= 10000000 && num < 20000000) {
            this.input1 = num * 0.006 + 21800 + "";
          } else if (num >= 20000000) {
            this.input1 = num * 0.005 + 31800 + "";
          }
          if (num < 10000) {
            this.input2 = "50";
          } else if (num >= 10000 && num < 500000) {
            this.input2 = num * 0.015 - 100 + "";
          } else if (num >= 500000 && num < 5000000) {
            this.input2 = num * 0.01 + 2400 + "";
          } else if (num >= 5000000 && num < 10000000) {
            this.input2 = num * 0.005 + 27400 + "";
          } else if (num >= 10000000) {
            this.input2 = num * 0.001 + 67400 + "";
          }
          if (num <= 1000) {
            this.input3 = "30";
          } else if (num > 1000 && num <= 100000) {
            this.input3 = num * 0.01 + 20 + "";
          } else if (num > 100000) {
            this.input3 = num * 0.005 <= 5000 ? num * 0.005 + 520 + "" : 5000;
          }
        } else if (this.radio == "2") {
          this.input1 = "500 - 1000";
          this.input2 = "50 - 500";
          this.input3 = "30";
        }
      } else if (this.value == "选项6") {
        this.input1 = "10";
        this.input2 = "50 - 500";
        this.input3 = "30";
      } else if (this.value == "选项7") {
        this.input1 = "100";
        this.input2 = "50 - 500";
        this.input3 = "30";
      } else if (this.value == "选项8") {
        this.input1 = "100";
        this.input2 = "50 - 500";
        this.input3 = "30";
      } else if (this.value == "选项9") {
        this.input1 = "50 - 100";
        this.input2 = "50 - 500";
        this.input3 = "30";
      }
    },
    changeValue() {
      this.input0 = "";
      this.input1 = "";
      this.input2 = "";
      this.input3 = "";
      this.radio = "2";
      if (
        this.value == "选项2" ||
        this.value == "选项6" ||
        this.value == "选项7" ||
        this.value == "选项8" ||
        this.value == "选项9"
      ) {
        this.shifou = false;
        this.flag = true;
      } else {
        this.shifou = true;
        this.flag = false;
      }
    },
    chongzhi() {
      this.input0 = "";
      this.input1 = "";
      this.input2 = "";
      this.input3 = "";
    },
  }
};
</script>

<style scoped>
/* *{
  margin: 0;
  padding: 0;
} */
html,
body,
#app {
  margin: 0;
  padding: 0;
  position: fixed;
  z-index: -999;
}
ul,
li {
  list-style: none;
}
.container {
  /* background: #ccc; */
  overflow: hidden;
  height: 1500px;
}
.top {
  margin-bottom: 30px;
  margin-top: 30px;
  /* margin-left: 40%; */
}
.el-icon-edit-outline {
  color: #1488f5;
  font-size: 28px;
}
.much {
  margin-bottom: 40px;
  width: 200px;
  height: 15px;
}
.leixing_father {
  margin-bottom: 30px;
  margin-left: 7px;
  width: 100%;
}
.leixing {
  font-size: 14px;
  color: #666;
}
.sheji {
  margin-left: -103px;
}
.sheji1 {
  font-size: 14px;
  color: #666;
}
.edu {
  margin-left: 6px;
  margin-bottom: 20px;
}
.edu1 {
  font-size: 14px;
  color: #666;
}
.jieguo {
  /* margin-left: 40%; */
  margin-bottom: 30px;
}
.jieguo1 {
  font-size: 22px;
}
.tianxie {
  font-size: 22px;
}
.money {
  /* margin-left: 40%; */
  margin-top: 15px;
}
.el-row {
  margin-bottom: 30px;
  /* margin-left: 45%; */
}
.el-input__icon {
  /* height: 80%; */
}
.three {
  font-size: 14px;
  color: #666;
}
.yuan {
  font-size: 14px;
}
.el-button--primary {
  width: 80px;
  height: 50px;
}
.el-radio-group {
  vertical-align: top;
}
.col-sm-10,
.col-md-8,
.col-xs-10 {
  text-align: center;
}
.el-input--suffix .el-input__inner{
  padding-left: 15px;
}
</style>





